.App {
  text-align: center;
 
}

#graphcont{
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#refresh{
  position: absolute;
  top: 16px;
  left: 100vw;
  transform: translateX(-200%);
  opacity: 50%;
  transition: all 0.2s;
}

#refresh:hover{
  opacity: 100%;
}
